import React from 'react'
import greenTick from 'Shared/assets/images/other/green_tick.svg'

const data = [
  {
    heading: 'Big data insight Mailshots',
    description: 'Lorem ipsum dolor sit amet ectetur. Aliquam placerat enim in tortor in id.'
  },

  {
    heading: 'Graduate Placement',
    description: 'Lorem ipsum dolor sit amet ectetur. Aliquam placerat enim in tortor in id.'
  },
  {
    heading: 'Emiratisation',
    description: 'Lorem ipsum dolor sit amet ectetur. Aliquam placerat enim in tortor in id.'
  },

]

const MediaPlan = () => {
  return data.map((elem, index) => (
    <div className='media-plan-container'>
      <div className='media-plan-sub-container'>
        <div>
          <img src={greenTick} />
        </div>
        <div>
          <div className='media-plan-heading'>
            {elem.heading}
          </div>
          <div className='media-plan-description'>
            {elem.description}
          </div>
        </div>
      </div>
    </div>
  ))
}

