import React from 'react'
import PricingCheckoutController from './PricingCheckoutController'

export default function PricingCheckoutPage (props) {
  const {getUserDetails} = props
  
  return (<React.Fragment> <PricingCheckoutController  userDetails={getUserDetails}/> </React.Fragment>)
}



